.div {
  position: relative;
  line-height: 30px;
  font-weight: 500;
}
.arrow-right-icon {
  position: relative;
  width: 24px;
  height: 24px;
  overflow: hidden;
  flex-shrink: 0;
}
.tag {
  position: relative;
  line-height: 30px;
  font-weight: 500;
  color: var(--text-gray-color-text1-2);
}
.docstatus,
.parent {
  align-items: center;
  justify-content: flex-start;
}
.parent {
  flex: 1;
  display: flex;
  flex-direction: row;
  gap: var(--gap-base);
}
.docstatus {
  border-radius: 4px;
  background-color: var(--status-warning-color-warning-fill);
  padding: var(--padding-12xs) 12px;
  color: var(--status-warning-color-warning-default);
}
.doc-header,
.docstatus,
.group {
  display: flex;
  flex-direction: row;
}
.group {
  align-items: center;
  justify-content: flex-end;
  gap: 48px;
  color: var(--text-gray-color-text1-2);
}
.doc-header {
  align-self: stretch;
  background-color: var(--bg-color-bg1-1);
  box-shadow: var(--border-inside-bottom);
  border-bottom: 1px solid var(--border-color-border1-1);
  padding: 32px var(--padding-45xl);
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.div5,
.tag1 {
  position: relative;
  line-height: 58px;
  font-weight: 500;
}
.div5 {
  align-self: stretch;
  font-size: var(--header-font-header3-medium-size);
  line-height: 36px;
  color: var(--text-gray-color-text1-3);
}
.div4 {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-5xl);
}
.a,
.link-icon {
  position: relative;
}
.link-icon {
  width: 32px;
  height: 32px;
  overflow: hidden;
  flex-shrink: 0;
}
.a {
  line-height: 36px;
  font-weight: 500;
  color: inherit;
  text-decoration: none;
}
.div6,
.title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}
.div6 {
  align-items: center;
  gap: 7px;
  font-size: var(--header-font-header3-medium-size);
  color: var(--status-info-color-info-default);
}
.title {
  align-self: stretch;
  padding: var(--padding-29xl) var(--padding-61xl) var(--padding-45xl);
  align-items: flex-start;
  gap: var(--gap-31xl);
}
.analysis-icon {
  position: relative;
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.text1 {
  position: relative;
  line-height: 24px;
}
.tag2 {
  border-radius: var(--br-11xs);
  background-color: var(--secondary-color-secondary-transp-fill);
  display: flex;
  flex-direction: row;
  padding: var(--padding-12xs) var(--padding-5xs);
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.delete-2-fill-icon {
  position: relative;
  width: 16px;
  height: 16px;
}
.tag3 {
  border-radius: var(--br-11xs);
  background-color: var(--status-danger-color-danger-fill);
  flex-direction: row;
  padding: var(--padding-12xs) var(--padding-5xs);
  align-items: center;
  gap: var(--gap-9xs);
  color: var(--status-danger-color-danger-default);
}
.tag3,
.tag4,
.tag5 {
  display: flex;
  justify-content: flex-start;
}
.tag4 {
  border-radius: var(--br-11xs);
  background-color: var(--status-info-color-info-fill);
  flex-direction: row;
  padding: var(--padding-12xs) var(--padding-5xs);
  align-items: center;
  gap: var(--gap-9xs);
  color: var(--status-info-color-info-default);
}
.tag5 {
  background-color: var(--status-invalid-color-invalid-fill);
  color: var(--text-gray-color-text1-3);
}
.tag5,
.tag6,
.tag7 {
  border-radius: var(--br-11xs);
  flex-direction: row;
  padding: var(--padding-12xs) var(--padding-5xs);
  align-items: center;
  gap: var(--gap-9xs);
}
.tag6 {
  background-color: var(--status-success-color-success-fill);
  display: flex;
  justify-content: flex-start;
  color: var(--status-success-color-success-default);
}
.tag7 {
  background-color: var(--status-warning-color-warning-fill);
  color: var(--status-warning-color-warning-default);
}
.component-showcase,
.tag-parent,
.tag7 {
  display: flex;
  justify-content: flex-start;
}
.tag-parent {
  flex-direction: row;
  align-items: flex-start;
  gap: 10px;
}
.component-showcase {
  align-self: stretch;
  background-color: var(--fill-color-fill1-1);
  flex-direction: column;
  padding: var(--padding-61xl) 4px var(--padding-101xl);
  align-items: center;
  font-size: var(--header-font-header5-medium-size);
  color: var(--text-gray-color-text1-2);
}
.p {
  margin: 0;
}
.div7 {
  flex: 1;
  position: relative;
  line-height: 30px;
  font-weight: 500;
}
.component-define,
.doc-defination {
  align-self: stretch;
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.component-define {
  flex-direction: row;
  padding: var(--padding-29xl) 400px var(--padding-81xl) var(--padding-61xl);
  font-size: var(--header-font-header4-medium-size);
  color: var(--text-gray-color-text1-3);
}
.doc-defination {
  background-color: var(--bg-color-bg1-1);
  border-bottom: 1px solid var(--border-color-border1-1);
  flex-direction: column;
  font-size: var(--header-font-header1-medium-size);
  color: var(--text-gray-color-text1-1);
}
.div8 {
  align-self: stretch;
  position: relative;
  line-height: 38px;
  font-weight: 500;
}
.oval-icon {
  position: absolute;
  bottom: 0;
  left: calc(50% - 10px);
  width: 20px;
  height: 20px;
}
.div9 {
  position: absolute;
  top: 0;
  left: 1px;
  line-height: 20px;
  font-weight: 500;
  display: inline-block;
  width: 18px;
}
.docnumber {
  position: relative;
  width: 20px;
  height: 20px;
}
.div10 {
  position: relative;
  font-size: var(--header-font-header5-medium-size);
  line-height: 24px;
  font-weight: 500;
  color: var(--decorative-purple-color-decorative4-1);
  text-align: left;
}
.docnumber-parent {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-9xs);
}
.div11 {
  flex: 1;
  position: relative;
  font-size: var(--header-font-header5-medium-size);
  line-height: 24px;
  font-weight: 500;
  color: var(--text-gray-color-text1-3);
  text-align: left;
}
.frame-container,
.frame-div {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.div17 {
  flex: 1;
  position: relative;
  font-size: var(--header-font-header5-medium-size);
  line-height: 24px;
  font-weight: 500;
  color: var(--text-gray-color-text1-3);
  text-align: left;
}
.frame-group,
.frame-parent1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.frame-group {
  flex-direction: column;
  gap: var(--gap-xs);
}
.image-1-icon {
  align-self: stretch;
  position: relative;
  max-width: 100%;
  overflow: hidden;
  height: 305px;
  flex-shrink: 0;
  object-fit: cover;
}
.frame-parent {
  align-self: stretch;
  align-items: flex-start;
  gap: 40px;
  text-align: center;
  font-size: var(--description-font-description-medium-size);
  color: var(--bg-color-bg1-1);
}
.doc-breakdown,
.doc-starter,
.frame-parent {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}
.doc-breakdown {
  align-self: stretch;
  background-color: var(--bg-color-bg1-1);
  border-bottom: 1px solid var(--border-color-border1-1);
  padding: 40px var(--padding-61xl) var(--padding-81xl);
  align-items: flex-start;
  gap: 60px;
  font-size: var(--header-font-header2-medium-size);
  color: var(--text-gray-color-text1-2);
}
.doc-starter {
  position: relative;
  width: 100%;
  align-items: flex-end;
  text-align: left;
  font-size: var(--header-font-header4-medium-size);
  color: var(--text-gray-color-text1-3);
  font-family: var(--header-font-header5-medium);
}
@media screen and (max-width: 960px) {
  .parent {
    flex: unset;
    align-self: stretch;
  }
  .doc-header {
    flex-direction: column;
    gap: var(--gap-5xl);
    align-items: flex-start;
    justify-content: flex-start;
  }
  .component-define {
    padding-right: 200px;
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .div4 {
    flex: unset;
    align-self: stretch;
  }
  .title {
    flex-direction: column;
    gap: var(--gap-31xl);
    align-items: flex-start;
    justify-content: flex-start;
  }
  .div11 {
    flex: unset;
    align-self: stretch;
  }
  .frame-container,
  .frame-div {
    flex-direction: column;
    gap: var(--gap-base);
    align-items: flex-start;
    justify-content: flex-start;
  }
}
